.navbar {
    background-color: #007bff;
    color: #fff;
  }
  
  .navbar ul {
    list-style-type: none;
    display: flex;
    justify-content: flex-end;
    padding: 0;
  }
  
  .navbar li {
    margin-right: 20px;
  }
  
  .navbar a {
    text-decoration: none;
    color: #fff;
  }
  
  .navbar a:hover {
    text-decoration: underline;
  }
  