.home-container {
    max-width: 1200px;
    margin: 20px auto;
  }
  
  .product {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #ddd;
    padding: 10px 0;
  }
  
  .product img {
    width: 100px;
    height: 100px;
    margin-right: 20px;
  }
  
  .product-info {
    flex-grow: 1;
  }
  
  .product-info h3 {
    margin: 0;
  }
  
  .product-info p {
    margin: 5px 0;
  }
  
  .product-price {
    font-weight: bold;
    color: #007bff;
  }
  